@keyframes mysticalJourney {
  to {
    transform: translate3d(0, 15px, 0);
  }
}

@keyframes mysticalJourneyMobile {
  to {
    transform: translate3d(0, 4px, 0);
  }
}

.mysticalJourneyWordsAnimation {
  @media (min-width: $stop-scaling) {
    max-width: 1000px;
  }

  & .text-animation-character {
    transform: translate3d(0, 150px, 0);

    animation-name: mysticalJourney;
    animation-duration: 0.5s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
  }

  & .text-animation-word:nth-of-type(1) {
    & .text-animation-character:nth-of-type(3),
    & .text-animation-character:nth-of-type(7),
    & .text-animation-character:nth-of-type(9),
    & .text-animation-character:nth-of-type(12) {
      animation-delay: 0s;
    }

    & .text-animation-character:nth-of-type(3),
    & .text-animation-character:nth-of-type(7) {
      animation-delay: 0.1s;
    }

    & .text-animation-character:nth-of-type(1),
    & .text-animation-character:nth-of-type(4),
    & .text-animation-character:nth-of-type(5),
    & .text-animation-character:nth-of-type(10),
    & .text-animation-character:nth-of-type(11) {
      animation-delay: 0.2s;
    }

    & .text-animation-character:nth-of-type(8) {
      animation-delay: 0.3s;
    }
  }

  & .text-animation-word:nth-of-type(3) {
    & .text-animation-character:nth-of-type(4),
    & .text-animation-character:nth-of-type(6) {
      animation-delay: 0.3s;
    }

    & .text-animation-character:nth-of-type(3) {
      animation-delay: 0.4s;
    }

    & .text-animation-character:nth-of-type(1),
    & .text-animation-character:nth-of-type(2),
    & .text-animation-character:nth-of-type(5) {
      animation-delay: 0.5s;
    }
  }
}

@media (max-width: $mobile) {
  .mysticalJourneyWordsAnimation .text-animation-character {
    animation-name: mysticalJourneyMobile;
  }
}

@keyframes dateLabel {
  to {
    opacity: 1;
  }
}

.dateLabelWordsAnimation {
  & .text-animation-character {
    opacity: 0;

    animation-name: dateLabel;
    animation-duration: 0.5s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;

    animation-delay: 1.2s;
  }
}

@keyframes date {
  to {
    transform: translate3d(0, 5px, 0);
  }
}

.dateWordsAnimation {
  & .text-animation-character {
    transform: translate3d(0, 75px, 0);

    animation-name: date;
    animation-duration: 0.3s;
    animation-timing-function: ease-in-out;
    animation-fill-mode: forwards;
  }

  & .text-animation-word:nth-of-type(9) .text-animation-character:nth-of-type(3) {
    animation-delay: 1s;
  }

  & .text-animation-word:nth-of-type(5) .text-animation-character:nth-of-type(2) {
    animation-delay: 1.01s;
  }

  & .text-animation-word:nth-of-type(3) .text-animation-character:nth-of-type(1) {
    animation-delay: 1.02s;
  }

  & .text-animation-word:nth-of-type(5) .text-animation-character:nth-of-type(5) {
    animation-delay: 1.03s;
  }

  & .text-animation-word:nth-of-type(7) .text-animation-character:nth-of-type(1) {
    animation-delay: 1.04s;
  }

  & .text-animation-word:nth-of-type(9) .text-animation-character:nth-of-type(4) {
    animation-delay: 1.05s;
  }

  & .text-animation-word:nth-of-type(1) .text-animation-character:nth-of-type(1) {
    animation-delay: 1.06s;
  }

  & .text-animation-word:nth-of-type(5) .text-animation-character:nth-of-type(3) {
    animation-delay: 1.07s;
  }

  & .text-animation-word:nth-of-type(9) .text-animation-character:nth-of-type(1) {
    animation-delay: 1.08s;
  }

  & .text-animation-word:nth-of-type(1) .text-animation-character:nth-of-type(2) {
    animation-delay: 1.09s;
  }

  & .text-animation-word:nth-of-type(5) .text-animation-character:nth-of-type(1) {
    animation-delay: 1.1s;
  }

  & .text-animation-word:nth-of-type(9) .text-animation-character:nth-of-type(2) {
    animation-delay: 1.11s;
  }

  & .text-animation-word:nth-of-type(5) .text-animation-character:nth-of-type(4) {
    animation-delay: 1.12s;
  }
}

@keyframes story {
  to {
    transform: translate3d(0, 2px, 0);
